body {
  text-align: center;
  font-family: Arial, sans-serif;
}

h1 {
  color: #333;
}

.loader, .error {
  color: red;
  display: none;
}

.breed-select {
  margin: 20px;
  padding: 10px;
  display: flex;
}

.cat-info {
  background-color: #f7f7f7;
  border: 1px solid #ccc;
  margin: 20px;
  padding: 10px;
  display: none;
}

img {
  width: 500px;
  height: 50%;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .5);
}

.loader {
  width: 96px;
  height: 96px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader:after, .loader:before {
  content: "";
  width: 96px;
  height: 96px;
  box-sizing: border-box;
  border: 2px solid #191717;
  animation: rotation 2s ease-in-out infinite;
  position: absolute;
  top: 0;
  left: 0;
}

.loader:after {
  border-color: #ff3d00;
  animation-delay: 1s;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

